@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body{font-family:'Raleway', sans-serif}.skip{position:relative;z-index:10000;left:3rem;color:#2370f4;transition:1s easein;text-decoration:none !important;top:-12rem}.skip:hover{color:#2370f4}.skip:focus{top:0rem}.pg-light{background:#fff !important}.active{color:#2370f4 !important}.logo{color:#2370f4;font-size:2rem;font-weight:600}.pg-mobile__toggle{display:block;position:static}.cart{position:relative}.cart .cart-ind{position:absolute;background:#dddddd;width:15px;height:15px;top:-3px;border-radius:50px;left:8px;color:#fff;padding:0rem 1rem 1.5rem 0.3rem}.banner{background:url(/static/media/smiley-2979107_1920.bace9dc3.jpg);width:100%;height:400px;background-repeat:no-repeat;background-position:4% 23%;background-repeat:no-repeat}.header-content{padding-top:8rem;font-weight:600;color:#fff}.down{height:30px;-webkit-animation-name:down;animation-name:down;will-change:transform;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-direction:alternate;animation-direction:alternate}.pg-card{width:15rem;height:21rem;line-height:1;margin-left:2rem;margin-top:2rem}.pg-card .ascii-face{background:#f8f8f8;height:9.5rem;padding:2rem 1rem 2rem 1rem;color:#2370f4}.pg-card .ascii-face p{transition:all 2s ease}.pg-card .ascii-face:hover .face{transform:scale(1.1)}.pg-card .adimg{width:16rem}.pg-card .pg-card__desc{padding:2rem}.cart-btn{background:#236ff4;color:#fff}.cart-btn:hover{color:#fff}@-webkit-keyframes down{0%{margin-top:0px}50%{margin-top:3px}100%{margin-top:5px}}@keyframes down{0%{margin-top:0px}50%{margin-top:3px}100%{margin-top:5px}}@media screen and (max-width: 800px){.pg-mobile__toggle{position:fixed;display:flex;right:14px;top:12px}}

